.container {
  height: 50vh;
  background-color: #ffffff;
  border-color: #ffffff;
  align-items: center;
  border-radius: 1rem;
  vertical-align: middle
}

@media(max-width:767px) {
  .container {
      height: fit-content
  }
}

.weather {
  width: 77%
}

@media(max-width:767px) {
  .weather {
      width: 100%
  }
}

.card {
  padding: 1rem;
  margin: 1.5rem 4vw;
  border-radius: 1rem;
  background-color: rgba(238, 237, 237, 0.22);
  border-color: rgba(238, 237, 237, 0.22);
  box-shadow: 5px 6px 6px 2px #e9ecef;
  text-align: center
}

@media(max-width:768px) {
  .card {
      margin: 1.5rem
  }
}

.title {
  justify-content: center;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%)
}

.title p {
  margin-bottom: 0.2rem;
  font-size: 1.2rem
}

.temp {
  font-size: 2.6rem;
  margin-bottom: 1rem
}

.header {
  color: #dddddd
}

.col-4 {
  padding: 0 0.2rem
}

.icon {
  align-self: flex-end;
  margin-right: -2.5rem;
  margin-top: -2rem;
  z-index: 999
}

.icon img {
  width:380; 
  height:380
}